@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-body {

}

.board-container {
  display: flex;
  justify-content: center;
  width: 100%;
}

.header {
  display: flex;
  justify-content: center;
  width: 100%;
  text-align: center;
  padding: 2em 0 0 0 ;
}

.title {
  font-family: Poppins;
  font-weight: 800;
  font-size: 3em;
  line-height: 0;
}

.subtitle {
  font-family: Poppins;
  font-weight: 200;
  font-size: 1em;
}

.square {
    overflow: hidden;
    border: 0.05em solid #727272;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 1em;
    height: 4em;
    width: 4em;
    cursor: pointer;
}

.board-row {
    height: 4em;
}

.board {
}

.piece-container {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
}

.piece-img {
    display: block;
    width: 100%;
    height: auto;
}

